import { fechtConjwt } from "../helpers/fecht";
import { types } from "../types/types";

export const prestadoresStartLoading = ()=>{

    return async(dispatch)=>{
        try {
            const res = await fechtConjwt('prest/getPrestadores')
            const body = await res.json();

            
            if(body.ok){
                const prestadores = body.prestadores;
                dispatch(prestadoresLoaded(prestadores))

            }

        } catch (error) {
            console.log(error)
        }


    }

}

const prestadoresLoaded = (prestadores)=>{
    return{
        type: types.prestadoresLoaded,
        payload: prestadores
    }
}



export const startSetingActivePrestador = (record)=>{

    return async(dispatch)=>{
        try {
            const res = await fechtConjwt(`prest/getPrestador/${record.key}`)
            const body = await res.json();

            
            if(body.ok){
                const prestadorInfo = body.prestador;

                const res = await fechtConjwt(`prest/getComments/${record.key}`)
                const { comentarios } = await res.json();

                


                dispatch(setPrestadorActive({...prestadorInfo, comentarios}))

            }

        } catch (error) {
            console.log(error)
        }


    }

}

const setPrestadorActive = (prestador)=>{

return{
    type: types.loadActivePrestador,
    payload: prestador
}

}

export const unsetPrestadorActive = ()=>{

    return{
        type: types.unloadActivePrestador,
        
    }
    
}


export const unsetPrestadores = ()=>{

    return{
        type: types.unloadprestadores,
        
    }
    
}

export const setComment = (comentario)=>{
    return async()=>{
        try {
            const resp = await fechtConjwt('prest/newComment', comentario, 'POST');
            const body = await resp.json();
            if(body.ok){
                console.log(body)
            }
         
        } catch (error) {
            console.log(error)
        }

}}
