import { types } from "../types/types"

const initialState = {

msgStatus: "Offline"

}

export const chatReducer = (state = initialState, action)=>{

    switch (action.type) {

        case types.changeMsgStatus:

        return{
            ...state,
            msgStatus: action.payload,
        }
    
        default:
            return state;
    }
    


}