import { types } from "../types/types"

const initialState = {

checking: true,
logged:false,
isAuthenticated: false

}

export const authReducer = (state = initialState, action)=>{

    switch (action.type) {

        case types.startChecking:

        return{
            ...state,
            checking:true
        };

        case types.finishChecking:

        return{
            ...state,
            checking:false

        };

        case types.authLogout:

        return{
            checking:false,
            isAuthenticated: false,
            

        }

        case types.authLogin:
            
            return{
                ...state,
                checking:false,
                isAuthenticated: true,
                ...action.payload
            };

        case types.authSocialLogin:
            
            return{
                ...state,
                checking:false,
                isAuthenticated: true,
                ...action.payload
            };

        default:
            return state;
    }
    


}