import React from 'react'
import { Form, Input,  Select, Row, Col, Button } from 'antd';



const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };



export const StepForm2 = () => {

    const onFinish = (values) => {
        console.log('Received values of form: ', values);
      };

      const [form] = Form.useForm();

      const { Option } = Select;

function onChange(value) {
  console.log(`selected ${value}`);
}

function onBlur() {
  console.log('blur');
}

function onFocus() {
  console.log('focus');
}

function onSearch(val) {
  console.log('search:', val);
}



    return (
        <>

<Row>
    <Col span={12}>
            <Form
    {...formItemLayout}
    form={form}
    name="register"
    size={"small"}
    labelAlign={"left"}
    onFinish={onFinish}
    initialValues={{
      residence: ['zhejiang', 'hangzhou', 'xihu'],
      prefix: '54',
    }}
    scrollToFirstError
  >

<Form.Item
      name="nombreSucursal"
      label="Nombre Sucursal"
      tooltip="Preferentemente ingresar la razon social!"
      rules={[{ required: true, message: 'Please input your nickname!', whitespace: true }]}
    >
      <Input placeholder="Sucursal 3 - Pacheco" />
</Form.Item>

<Form.Item
name="provincia"
label="Provincia"
rules={[{ required: true, message: 'Please input your nickname!', whitespace: true }]}
>

<Select
    showSearch
    placeholder="Buenos Aires"
    optionFilterProp="children"
    onChange={onChange}
    onFocus={onFocus}
    onBlur={onBlur}
    onSearch={onSearch}
 >

    <Option value="jack">Buenos Aires</Option>
    <Option value="lucy">Santa Fé</Option>
    <Option value="tom">Córdoba</Option>
    <Option value="tom">San Juan</Option>
    <Option value="tom">Corrientes</Option>
  </Select>


</Form.Item>

<Form.Item
name="localidad"
label="Localidad"
rules={[{ required: true, message: 'Please input your nickname!', whitespace: true }]}
>

<Select
    showSearch
    placeholder="Berazategui"
    optionFilterProp="children"
    onChange={onChange}
    onFocus={onFocus}
    onBlur={onBlur}
    onSearch={onSearch}
 >
     
    <Option value="jack">CABA</Option>
    <Option value="lucy">Quilmes</Option>
    <Option value="tom">Berazategui</Option>
    </Select>


</Form.Item>


<Form.Item wrapperCol={{ offset: 8, span: 16 }}>
        <Button type="primary" htmlType="submit">
          Cargar
        </Button>
      </Form.Item>

</Form>

</Col>
</Row>



        </>
    )
}
