export const Sucursales = () => {
    const columns = [
        {
          title: 'Sucursal',
          dataIndex: 'name',
          key: 'name',
            
        },
        {
          title: 'Domicilio',
          dataIndex: 'domicilio',
          key: 'domicilio',
          
        },
        {
            title: 'Telefono',
            dataIndex: 'telefono',
            key: 'telefono',
            
          },
      
      ];
    return (
        <>
            <Table size={"middle"} columns={columns} dataSource={data} />
            <Map />
        </>
    )
}
