import React, { useEffect } from 'react';
import { createContext } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setUsuariosConectados } from '../actions/socket';
import { useSocket } from '../hooks/useSocket'

import { notification } from 'antd';
import Avatar from 'antd/lib/avatar/avatar';

export const SocketContext = createContext();


export const SocketProvider = ({ children }) => {

    const { socket, online, conectarSocket, desconectarSocket } = useSocket('https://prestadores.herokuapp.com');
    const { logged, uid } = useSelector( state => state.auth );
    const { msgStatus } = useSelector( state => state.chat );
    const dispatch = useDispatch();
    
    useEffect(() => {
        if(logged){
           conectarSocket()
        }
    }, [logged, conectarSocket])
    
    useEffect(() => {
        if(!logged){
           desconectarSocket()
        }
    }, [logged, desconectarSocket])
   
   //Escuchar cambios en usuarios conectados y mostrar lista
    useEffect(() => {
        socket?.on('usuarios-activos',(usuarios)=>{
            dispatch(setUsuariosConectados(usuarios));
            
        })
    }, [ socket, dispatch ])
   
    useEffect(() => {
        socket?.on('nuevo-usuario-conectado',(usuario)=>{
            usuario.uid !== uid &&
            openNotification(usuario)
        })
    }, [ socket, uid ])
   
    const openNotification = usuario => {
        notification.info({
          message: `${usuario.nombre} se conectó`,
          description: '',
            icon: <Avatar size={30} src={usuario.photoURL} />,
            placement: 'bottomRight',
        });
      };

      useEffect(() => {
        socket?.emit('change-user-status',[msgStatus, uid]); 
    
      }, [socket, uid, msgStatus])
      
    return (
        <SocketContext.Provider value={{ socket, online }}>
            { children }
        </SocketContext.Provider>
    )
}