import React from 'react';
import { useDispatch } from 'react-redux';

import { useForm } from '../../hooks/useForm';
import { Link } from 'react-router-dom';

import { startGoogleLogin,
         startLogin } from '../../actions/auth';



import {  Form, 
          Input, 
          Button, 
          Space, 
          Col, 
          Row, 
          Divider } from 'antd';

import { UserOutlined,
         LockOutlined,
         GoogleOutlined  } from '@ant-design/icons';

export const LoginScreen = () => {

    const [ formValues, handleInputChange] = useForm( {email: '', password: ''} );
    const { email, password } = formValues;

    const dispatch = useDispatch();

    
     const handleLogin = (e)=>{
      
        dispatch(startLogin(email,password));
        e.preventDefault();
        
      }
     
   

  const handleGoogleLogin = (e)=>{
   
    dispatch(startGoogleLogin())
    e.preventDefault();
};

    return (

        <>
        <Row className="auth__login-row"align={'middle'}>
            <Col>
        <section className="auth__code-box">
            <Space>
        <Form
         
        >
      <Form.Item
        name="email"
        rules={[{ required: true, message: 'Ingresá tu usuario!' }]}
       >
        <Input 
        prefix={<UserOutlined className="auth__site-form-item-icon" />}
        placeholder="Username"
        value={ email }
        onChange={ handleInputChange }
        />
        </Form.Item>
      
      <Form.Item
        name="password"
        rules={[{ required: true, message: 'Ingresá tu password!' }]}
      >
        <Input
          prefix={<LockOutlined className="auth__site-form-item-icon" />}
          type="password"
          placeholder="Password"
          value= { password }
          onChange= { handleInputChange }
         />
      </Form.Item>
      
      <Form.Item>
        <Space>
        <Button type="primary" htmlType="submit" className="login-form-button" onClick={handleLogin}>
          Ingresá
        </Button>
         O  <Link className="link" to="/auth/register">Registrate!</Link>
         </Space>
      </Form.Item>
      <Divider plain>Social Login</Divider>
      <Form.Item>
      <Button type="primary" className="auth__login-form-button" onClick={handleGoogleLogin}>
      <GoogleOutlined />  Ingresá con Google
        </Button>
          
      </Form.Item>
    </Form>
    </Space>
    </section>         
            </Col>
        </Row>
   
   

    

                </>


       
   )
}
