
import React from 'react';
import { Row, Col,  Button, Steps, message } from 'antd';
import { RightCircleOutlined, LeftCircleOutlined } from '@ant-design/icons';


import { StepForm1 } from './StepForm1';
import { StepForm2 } from './StepForm2';
import { StepForm3 } from './StepForm3';


const { Step } = Steps;

const steps = [
    {
      step: 1,
      title: "Datos Basicos",
      content: <StepForm1 />
    },
    {
      step: 2,
      title: "Sucursales",
      content: <StepForm2 />
    },
    {
      step: 3,
      title: "Documentación",
      content: <StepForm3 />
      }
];

export const FormPrestador = () => {
 
const [current, setCurrent] = React.useState(0);

  const next = () => {
    setCurrent(current + 1);
  };

  const prev = () => {
    setCurrent(current - 1);
  };
    return (
        <>

        <Row>
            <Col span={24}>
                <h2>Alta de Prestadores</h2>   
            </Col>
        </Row>

        <Row>
        <Col span={24}>
            <Steps current={current}>
        {steps.map(item => (
          <Step key={item.title} title={item.title} />
        ))}
      </Steps>
      <div className="formprestador__steps-content">{steps[current].content}</div>
      <div className="formprestador__steps-action">
        {current < steps.length - 1 && (
          <Button type="primary" onClick={() => next()}>
            Siguiente <RightCircleOutlined />
          </Button>
        )}
        {current === steps.length - 1 && (
          <Button type="primary" onClick={() => {message.success('Processing complete!')}}>
            Confirmar
          </Button>
        )}
        {current > 0 && (
          <Button style={{ margin: '0 8px' }} onClick={() => prev()}>
            Anterior <LeftCircleOutlined />
          </Button>
        )}
      </div>
      </Col>
        </Row>
    

   
 
 
        </>
    )
}
