import React, { useState } from 'react'
import { PageHeader,
         Descriptions,
         Button,
         Badge,
         Divider,
         Timeline,
         Row,
         Col,
         Tag,
         Typography,
         Space,
        Rate } from 'antd';

import { CommentUi } from '../ui/CommentUi';
import { Link } from 'react-router-dom';
import { DeleteOutlined, 
         EditOutlined,
         CloudOutlined,
         MailOutlined,
         DollarCircleOutlined,
         PercentageOutlined,
         LeftCircleOutlined,
         PhoneOutlined,
         WhatsAppOutlined,
         } from '@ant-design/icons';
import { ValoresEstudios } from './ValoresEstudios';
import { useSelector } from 'react-redux';
 
const { Paragraph, Text } = Typography;

const IconLink = ({ src, text }) => (
  <a className="fileprestador__example-link" href="/">
    <img className="fileprestador__example-link-icon" src={src} alt={text} />
    {text}
  </a>
);

const content = (
  <>
    <Paragraph>
      Ant Design interprets the color system into two levels: a system-level color system and a
      product-level color system.
    </Paragraph>
    <Paragraph>
      Ant Design&#x27;s design team preferred to design with the HSB color model, which makes it
      easier for designers to have a clear psychological expectation of color when adjusting colors,
      as well as facilitate communication in teams.
    </Paragraph>
    <div className="fileprestador__linkrow">


    <IconLink
        src="https://gw.alipayobjects.com/zos/rmsportal/MjEImQtenlyueSmVEfUD.svg"
        text="Quick Start"
       
      />

      <IconLink
        src="https://gw.alipayobjects.com/zos/rmsportal/NbuDUAuBlIApFuDvWiND.svg"
        text="Log de Cambios"
      />
      
      <IconLink
        src="https://gw.alipayobjects.com/zos/rmsportal/ohOEPSYdDTNnyMbGuyLb.svg"
        text="Notas"
      />

      
    </div>
  </>
);

const Content = ({ children, extraContent }) => (
  <Row>
    <div style={{ flex: 1 }}>{children}</div>
    <div className="image">{extraContent}</div>
  </Row>
);

  
export const FilePrestador = () => {
    const { uid } = useSelector( state => state.auth );
    const { nombre, telefono, whatsapp, email, copago, descuento, web, domicilio, comentarios, especialidad } = useSelector( state => state.prestadores.activePrestador );
   
    
const routes = [
  {
    path: '/',
    breadcrumbName: 'Prestadores',
  },
  {
    path: 'first',
    breadcrumbName: `${ nombre }`,
  },
  
];



    const comments = comentarios.map(comentario => {
    
    if(uid === comentario.user){
     return {
      id:comentario.id,
      author: comentario.name,
      avatar: {src:comentario.photoURL,alt:'User Img'},
      content: comentario.comentario,
      datetime: comentario.fecha,
      canEdit: true    
    }}else{

      return {
        id:comentario.id,
        author: comentario.name,
        avatar: {src:comentario.photoURL,alt:'User Img'},
        content: comentario.comentario,
        datetime: comentario.fecha,
        canEdit: false

    }
  }
    
  })

  const [commentsCounter, setCommentsCounter] = useState(2);
  
const loadComments = ()=>{

  if(comments.length > 2 && commentsCounter <= comments.length){
   const newComments = comments.slice(0,commentsCounter);
    
   return newComments.map(comment=>(
                  
    <CommentUi key={comment.id}
               author={comment.author}
               src={comment.avatar.src}
               alt={comment.avatar.alt}
               content={comment.content}
               datetime={comment.datetime}
               editable={comment.canEdit}/>
  ))
    }else{


    return comments.map(comment=>(
                  
      <CommentUi key={comment.id}
                 author={comment.author}
                 src={comment.avatar.src}
                 alt={comment.avatar.alt}
                 content={comment.content}
                 datetime={comment.datetime}
                 editable={comment.canEdit}/>
    ))
  

  }


}

    return (
        <>

           <PageHeader
    title={ nombre }
    className="fileprestador__site-page-header"
    subTitle="Consultorio Privado"
    tags={especialidad.map(especialidad=>(<Tag key={ especialidad }color="blue">{ especialidad }</Tag>))}
    extra={[
      <Button key="2bns"><DeleteOutlined /> Eliminar</Button>,
      <Button key="3bnt"><EditOutlined /> Editar</Button>,
      <Button key="1btn" type="primary">
         <Link to="/prestadores"> <LeftCircleOutlined /> Volver </Link>
      </Button>,

    ]}
    avatar={{ src: 'https://avatars1.githubusercontent.com/u/8186664?s=460&v=4' }}
    breadcrumb={{ routes }}
  >
    <Content
      extraContent={
        <img
          src="https://gw.alipayobjects.com/zos/antfincdn/K%24NnlsB%26hz/pageHeader.svg"
          alt="content"
          width="100%"
        />
      }
    >
      {content}
    </Content>
  </PageHeader>  
            
            <Divider orientation="left" plain>Datos Básicos</Divider>

    <Descriptions size="small" bordered>
      <Descriptions.Item label={<span><MailOutlined /> Email</span>}><Paragraph copyable>{ email }</Paragraph></Descriptions.Item>
      <Descriptions.Item label={<span><PhoneOutlined /> Telefono</span>}><Paragraph copyable>{ telefono }</Paragraph></Descriptions.Item>
      <Descriptions.Item label={<span><WhatsAppOutlined /> WhatsApp</span>}><Paragraph copyable>{ whatsapp }</Paragraph></Descriptions.Item>
      <Descriptions.Item label={<span><CloudOutlined /> Pagina Web</span>}><Paragraph copyable>{ web }</Paragraph></Descriptions.Item>
      <Descriptions.Item label="Domicilio" span={2}>
      { domicilio }
      </Descriptions.Item>
      <Descriptions.Item label={<span><DollarCircleOutlined /> Copago</span>}>{ copago }</Descriptions.Item>
      <Descriptions.Item label={<span><PercentageOutlined /> Descuento</span>}>{ descuento }</Descriptions.Item>
      <Descriptions.Item label="Nivel de Precio"><Text type={"success"}>Normal</Text></Descriptions.Item>
      <Descriptions.Item label={<span>Status</span>}><Badge status="processing" text="Activo" /></Descriptions.Item>
      <Descriptions.Item label={<span>Ranking</span>}><Rate allowHalf defaultValue={2.5} /></Descriptions.Item>
      <Descriptions.Item label={<span> </span>}></Descriptions.Item>
  </Descriptions>

            <Divider orientation="left" plain id="Notes">Valores Estudios</Divider>
      <ValoresEstudios />
            <Divider orientation="left" plain id="Notes">Notes</Divider>
            
            {loadComments()}
            {

              
                (commentsCounter < comments.length)?

                <Text type="secondary" onClick={(e)=>{setCommentsCounter(commentsCounter + 2)}}>Cargar Más...</Text>           
                :

                ( comments.length === 2 )?
                <Text type="secondary" onClick={(e)=>{setCommentsCounter(2)}}></Text>
                :
                <Text type="secondary" onClick={(e)=>{setCommentsCounter(2)}}>Ocultar...</Text>

}
            
                
            <Divider orientation="left" plain id="TimeLine">Time Line</Divider>
    <Row>
           <Col>
            <Timeline mode={"left"}>
                <Timeline.Item label="2015-09-01">Prestador Creado por <Text type="secondary">Leandro Cordero</Text></Timeline.Item>
                <Timeline.Item label="2015-09-01 09:12:11">Modificado por <Text type="secondary">Leandro Cordero</Text> <Space size={1}> <Tag color="green">Telefono</Tag> <Tag color="lime">Domicilio</Tag> </Space></Timeline.Item>
                <Timeline.Item label="2015-09-01 09:12:11">Network problems being solved</Timeline.Item>
            </Timeline>
            </Col>
    </Row>
            </>
    )
}
