import React from 'react';
import { useDispatch } from 'react-redux';
import { uiOpenModal } from '../../actions/ui';
import { AddComment } from './AddComment';
import { Sucursales } from './Sucursales';
import { ValoresEstudios } from './ValoresEstudios';

export const PopUp = ({record, visible, x, y}) => {
  
  const dispatch = useDispatch();

  const showModal = (e)=>{
    
    switch (e.target.id) {
      case "estudios":
          dispatch(uiOpenModal({component: <ValoresEstudios />, title: 'Valores de Estudios',record: record}));
          break;
      case "sucursales":
          dispatch(uiOpenModal({component: <Sucursales />, title: 'Sucursales',record: record}));
          break;
      case "notas":
          dispatch(uiOpenModal({component: <AddComment />, title: 'Comentarios',record: record}));
          break;
      
          default:
            break;
    
        }
  } 

if(visible){

  return(
    
    <>
    <ul className="gridprestdores__popup" style={{left: `${x}px`, top: `${y}px`}}>
      <li id="estudios" onClick={showModal}>Ver Estudios</li>
      <li id="sucursales" onClick={showModal}>Ver Sucursales</li>
      <li id="notas" onClick={showModal}>Cargar Nota</li>
    </ul>
    </>)
    
}else{
  return null
}
  

}



