export const ValoresEstudios = () => {

    const columns = [
        {
          title: 'Estudio',
          dataIndex: 'name',
          key: 'name',
            
        },
        {
          title: 'Precio',
          dataIndex: 'price',
          key: 'price',
          
        },
      
      ];
    return (
        <>
            <Table size={"middle"} columns={columns} dataSource={data} />
        </>
    )
}
