import React from 'react'
import { Statistic, Row, Col, Card } from 'antd';
import { useSelector } from 'react-redux';
import { UserList } from './UserList';


export const DashboardUi = () => {

const { usuariosActivos } = useSelector( state => state.socket );


    return (
        <>
            <div className="alert">
         
             

<div className="site-statistic-demo-card">
    <Row gutter={16}>
      <Col span={12}>
        <Card>
        <Statistic title="Active Users" value={usuariosActivos.length} />
        </Card>
      </Col>
      <Col span={12}>
        <Card>
        <UserList users={ usuariosActivos } /> 
    
        </Card>
      </Col>
    </Row>
  </div>,
              
     
        </div>
         
        </>
    )
}
