import React, { useEffect, useRef } from 'react'
import mapboxgl from 'mapbox-gl';
import { useState } from 'react';

mapboxgl.accessToken = 'pk.eyJ1IjoibGVhbmRyb2NvcmRlcm8iLCJhIjoiY2txcjBvZmppMjkzeDJvbXZlb2MzNzYzeSJ9.vOcN5AyV3EQTYDfCx5Q5wQ';



export const Map = () => {

    const mapDiv = useRef()
const [, setMapa] = useState()
const [coords] = useState({lng: -58.258634099999995, lat: -34.7251644, zoom: 10})

useEffect(() => {
  
    const sucursales = [{coordinates:{lng:-58.26667,lat:-34.71667},
                         nombre: 'Sede principa',
                         domicilio: 'Lavalle N° 654',
                         especialidad:'Cardiologia'},
                         /*{coordinates:{lng:-58.2657975,lat:-34.7173192},
                         nombre: 'Consultorio Quilmes 2',
                         domicilio: 'Lavalle N° 654',
                         especialidad:'Cardiologia'},
                         {coordinates:{lng:-58.2810452,lat:-34.7128102},
                         nombre: 'Consultorio Bernal',
                         domicilio: 'Belgrano N° 253',
                         especialidad:'Cardiologia'},*/
                        ]


  const map = new mapboxgl.Map({
    container: mapDiv.current,
    style: 'mapbox://styles/mapbox/streets-v11',
    center: [coords.lng, coords.lat],
      zoom: coords.zoom
    });
    
    sucursales.forEach(sucursal=>{


      new mapboxgl.Marker()
          .setLngLat([sucursal.coordinates.lng, sucursal.coordinates.lat])
          .setPopup(new mapboxgl.Popup({ offset: 25 }) // add popups
          .setHTML(`<h3>Nombre: ${ sucursal.nombre }</h3><p>${ sucursal.domicilio }</p>`))
      .addTo(map);

    })
       
   
    map.addControl(new mapboxgl.NavigationControl());
    setMapa( map )
    
}, [ coords.lat, coords.lng, coords.zoom/*, devicesData*/ ])

    return (
        <>
          <div className="myMap"
     ref={ mapDiv } style={{ minHeight: 300 }}
        />


        </>
    )
}
