import React from 'react'
import { useDispatch, useSelector } from 'react-redux';

import { Avatar, Typography, Space, Badge, Menu, Dropdown } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import { changeMsgStatus } from '../../actions/chat';

export const UserOptions = () => {

    
    const { Text } = Typography;

    const { name, photoURL } = useSelector( state => state.auth );
    
    const dispatch = useDispatch();

    const changeStatus = (status)=>{
      dispatch(changeMsgStatus(status));
       }


    const menu = (
        <Menu>
          <Menu.Item key="Online" onClick={e=>changeStatus(e.key)}>
            Online
            
          </Menu.Item>
          <Menu.Item key="Busy" onClick={e=>changeStatus(e.key)}>
            Busy
          </Menu.Item>
          <Menu.Item key="On Phone" onClick={e=>changeStatus(e.key)}>
              On Phone
          </Menu.Item>
        </Menu>
      );

   
    return (
        <div className="navsettings__container">
                    <Space>
                     
                        <Text type="secondary">{ name }</Text>
                       
                           
                         <div>
                         <Dropdown overlay={menu} placement="bottomRight" arrow>
                         <Badge count={5} size={"small"}>
                         { photoURL ?
                         
                     <Avatar shape="square" size={45} src={photoURL} />
                    :
                    <Avatar shape="square" size={45} icon={<UserOutlined />} />
                    
                    }
                        </Badge>
                        </Dropdown>
                         </div>
               
                    </Space>
               
               </div>
 
    )
}
