import React, { useEffect, useState } from 'react'
import { Table, Tag, Button, Space } from 'antd';
import { EditOutlined, DeleteOutlined, SearchOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import { PopUp } from './PopUp';
import { ModalComponent } from '../ui/ModalComponent';
import { useCallback } from 'react';
import { prestadoresStartLoading, startSetingActivePrestador } from '../../actions/prestadores';
import { useDispatch, useSelector } from 'react-redux';

  
    
  
export const GridPrestadores = () => {
  
  const dispatch = useDispatch();
  

  const { prestadoresList } = useSelector( state => state.prestadores );



  const data = prestadoresList.map(prestador=>(


    {
      key: prestador.id,
      nombre: prestador.nombre,
      zona: prestador.zona,
      direccion: prestador.domicilio,
      especialidad: prestador.especialidad,
    }

  ))
  
  
  const [info, setInfo] = useState({
    filteredInfo: null,
    sortedInfo: null,
  });
  
  const { filteredInfo, sortedInfo } = info
  

  const columns = [
    {
      title: 'Nombre',
      dataIndex: 'nombre',
      key: 'nombre',
      render: text => <Link to="/prestadores/consulta">{text}</Link>
    },
    {
      title: 'Dirección',
      dataIndex: 'direccion',
      key: 'direccion',
    },
    {
      title: 'Zona',
      dataIndex: 'zona',
      key: 'zona',
      filters: [
        { text: 'Zona SUR', value: 'AMBA, Zona SUR' },
        { text: 'Zona NORTE', value: 'AMBA, Zona NORTE' },
        { text: 'Zona OESTE', value: 'AMBA, Zona OESTE' },
        { text: 'CABA', value: 'CABA' },
      ],
      filteredValue: filteredInfo?.zona || null,
      onFilter: (value, record) => record.zona.includes(value),
      sorter: (a, b) => a.zona.length - b.zona.length,
      sortOrder: sortedInfo?.columnKey === 'zona' && sortedInfo.order,
      ellipsis: true,
    },
    {
      title: 'Especialidad',
      key: 'especialidad',
      dataIndex: 'especialidad',
      render: especialidad => (
        <span>
          {especialidad.map(tag => {
            let color = tag.length > 10 ? 'geekblue' : 'green';
            if (tag === 'loser') {
              color = 'volcano';
            }
            return (
              <Tag color={color} key={tag}>
                {tag}
              </Tag>
            );
          })}
        </span>
      ),
      filters: [
        { text: 'Paediatría', value: 'Pediatría' },
        { text: 'Traumatología', value: 'Traumatología' },
        { text: 'Cardiología', value: 'Cardiología' },
        { text: 'Cirugía', value: 'Cirugía' },
        { text: 'Odontología', value: 'Odontología' }
      ],
      filteredValue: filteredInfo?.especialidad || null,
      onFilter: (value, record) => record.especialidad.includes(value),
    //  sorter: (a, b) => a.especialidad.length - b.especialidad.length,
      //sortOrder: sortedInfo?.columnKey === 'especialidad' && sortedInfo.order,
      ellipsis: false,

    },
    {
      title: 'Action',
      key: 'action',
      render: (text, record) => (
        <Space size="middle">
           <Link to="/prestadores/consulta"><EditOutlined /></Link>
           <Link to="/prestadores/consulta"><DeleteOutlined /></Link>
          <Link to="/prestadores/consulta"><SearchOutlined /></Link>
        </Space>
      ),
    },
  ];

const handleChange = (pagination, filters, sorter) => {
  //console.log('Various parameters', pagination, filters, sorter);
  setInfo({
    filteredInfo: filters,
    sortedInfo: sorter,
  });
};

const clearFilters = () => {
  setInfo({ filteredInfo: null });
};

const clearAll = () => {
  setInfo({
    filteredInfo: null,
    sortedInfo: null,
  });
};

const setAgeSort = () => {
  setInfo({
    sortedInfo: {
      order: 'descend',
      columnKey: 'nombre',
    },
  });
};

const [popup, setPopup] = useState({
  visible: false, 
  x: 0, 
  y: 0,
  record: null
})


const handleClick = useCallback(() => {
  !popup.visible && setPopup({...popup,
    visible:false})
}, [popup]);


useEffect(() => {
  dispatch(prestadoresStartLoading());
  document.addEventListener("click", handleClick);
 
return()=>{
  document.removeEventListener("click", handleClick);
 }
}, [dispatch, handleClick])





const eventRow = (record, rowIndex) => {
  return {
    onClick: event => {}, // click row
    onDoubleClick: event => {}, // double click row
    onContextMenu: event => {
      
       setPopup({visible:true,
                x: event.clientX,
                y: event.clientY,
                record: record})

        event.preventDefault();
             
    

    }, // right button click row
    onMouseEnter: event => {dispatch(startSetingActivePrestador(record))}, // mouse enter row
    onMouseLeave: event => {}, // mouse leave row
  };
}

  return (
    <>
    <ModalComponent/>
  
    <Space style={{ marginBottom: 16 }}>
          <Button onClick={ setAgeSort }>Ordenar Nombre</Button>
          <Button onClick={ clearFilters }>Limpiar Filtros</Button>
          <Button onClick={ clearAll }>Limpiar todo</Button>
    </Space>
    

    
          <Table
          columns={columns}
          onRow={eventRow}          
          pagination={{ position: 'bottomCenter', pageSize: 12, pageSizeOptions: [5,10,25,50] }}
          dataSource={data}
          onChange={ handleChange }
        />
        <PopUp {...popup} />
          
        </>
    )
}
