import { types } from "../types/types"

const initialState = {

usuariosActivos: [],

}

export const socketReducer = (state = initialState, action)=>{

    switch (action.type) {

        case types.loadUsuariosActivos:

        return{
            ...state,
            usuariosActivos: action.payload
        };

           
        default:
            return state;
    }
    


}