import { types } from "../types/types";


const initialState = {

    prestadoresList:[],
    activePrestador: null

}

export const prestadoresReducer = (state = initialState, action) =>{

    switch ( action.type ) {
        
        case types.prestadoresLoaded:

                return{...state,
                    prestadoresList: [...action.payload]
                }

        case types.unloadprestadores:

                return{...state,
                    prestadoresList: []
                    }
    

        case types.loadActivePrestador:

                return{...state,
                    activePrestador: action.payload
                }

        case types.unloadActivePrestador:

                    return{...state,
                        activePrestador: null
                    }
    
            default:
                return state;
        }



}