import React, { useState } from 'react'
import {  Row, 
          Col, 
          Avatar, 
          Comment, 
          Tooltip } from 'antd'

import { DislikeOutlined, LikeOutlined, DislikeFilled, LikeFilled, EditOutlined } from '@ant-design/icons';



export const CommentUi = (props) => {

  const [likes, setLikes] = useState(0);
  const [dislikes, setDislikes] = useState(0);
  const [action, setAction] = useState(null);

  const like = () => {
    setLikes(1);
    setDislikes(0);
    setAction('liked');
  };

  const dislike = () => {
    setLikes(0);
    setDislikes(1);
    setAction('disliked');
  };


  const actions = [
    <Tooltip key="comment-basic-like" title="Like">
      <span onClick={like}>
        {React.createElement(action === 'liked' ? LikeFilled : LikeOutlined)}
        <span className="comment-action">{likes}</span>
      </span>
    </Tooltip>,
    <Tooltip key="comment-basic-dislike" title="Dislike">
      <span onClick={dislike}>
        {React.createElement(action === 'disliked' ? DislikeFilled : DislikeOutlined)}
        <span className="comment-action">{dislikes}</span>
      </span>
    </Tooltip>,
   
  ];

if(props.editable){
  actions.push(<span key="comment-basic-edit"> Edit Comment <EditOutlined /> </span>)
}else{
  actions.push(<span key="comment-basic-reply-to">Reply to</span>)
}

  
    return (
        <>
            <Row>
           <Col span={20}>
           <Comment className="comments__comment-container"
      actions={actions}
      author={ props.author}
      avatar={
        <Avatar
          src={props.src}
          alt={props.alt}
        />
      }
      content={
        
        <p>
          {props.content}
        </p>
        
      }
      datetime={
        <Tooltip title={props.datetime}>
          <span>{props.datetime}</span>
        </Tooltip>
      }
    />
           </Col> 
    </Row>    
        
    
        </>
    )
}
