import React from 'react'
import { Form, Input,  Button, message } from 'antd';
import { useDispatch, useSelector } from 'react-redux';
import { useForm } from '../../hooks/useForm';
import { setComment } from '../../actions/prestadores';
import { uiCloseModal } from '../../actions/ui';

export const AddComment = () => {

    const success = () => {
        message.success({
          content: 'Comentario cargado correctamente',
          className: 'custom-class',
          style: {
            marginTop: '5vh',
          },
        });
      };

const handleRegister = (e)=>{
    e.preventDefault();
    const { id } = prestadores.activePrestador;
    const comentario = {fecha: Date.now(),
                    comentario: textComentario,
                    prestadorId: id}
    dispatch(setComment(comentario));
    reset();
    dispatch(uiCloseModal());
    success()

}

const [ formValues, handleInputChange, reset] = useForm( {textComentario:''} );
const dispatch = useDispatch();
const { textComentario } = formValues;
const { prestadores } = useSelector( state => state );



    return (
        <>
      <Form name="nest-messages" onSubmit={handleRegister}>
      <Form.Item>
      

        <Input.TextArea
        id="textComentario"
        name="textComentario"
        value={ textComentario }
        onChange={ handleInputChange }
             />

      </Form.Item>
      <Form.Item>
        <Button type="primary" htmlType="submit" onClick={handleRegister}>
          Cargar
        </Button>
      </Form.Item>
    </Form> 
        </>
    );
};
