import React, { useState } from 'react'
import { Layout, Menu } from 'antd';
import {
  PieChartOutlined,
  LogoutOutlined,
  UserOutlined,
  MailOutlined,
} from '@ant-design/icons';
import { UserOptions } from './UserOptions';
import {
  Link,
  Switch,
  Route,
  Redirect
} from "react-router-dom";

import { GridPrestadores } from '../prestadores/GridPrestadores';
import { FilePrestador } from '../prestadores/FilePrestador';
import { DashboardUi } from './DashboardUi';
import { FormPrestador } from '../prestadores/FormPrestador';
import { startLogOut } from '../../actions/auth';
import { useDispatch } from 'react-redux';

export const UiMainScreen = (props) => {

    const dispatch = useDispatch();
    const { Header, Content, Footer, Sider } = Layout;
    const { SubMenu } = Menu;

    const [collapsed, setCollapsed] = useState(true);

   
    const handleLogout = ()=>{
    
      dispatch(startLogOut())
    }
    
    const onCollapsed = ()=>{
        
        if(collapsed){ setCollapsed(false);
        }else{
            setCollapsed(true);

        }
       
    }

    return (
        <>
     <Layout style={{ minHeight: '100vh' }}>
        <Sider collapsible collapsed={collapsed} onCollapse={onCollapsed}>
          <div className="logo" style={{ minHeight: '7.4vh' }} />
          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
            <Menu.Item key="1" icon={<PieChartOutlined />}>
            <Link to="/dash">Dashboard</Link>
            </Menu.Item>
            <SubMenu key="sub1" icon={<UserOutlined />} title="Prestadores">
              <Menu.Item key="2"><Link to="/prestadores">Consultar</Link></Menu.Item>
              <Menu.Item key="3"><Link to="/prestadores/agregar">Cargar</Link></Menu.Item>
              <Menu.Item key="4"><Link to="/prestadores/consulta">Actualizar</Link></Menu.Item>
            </SubMenu>
              <Menu.Item key="5" icon={<MailOutlined />}>Messages</Menu.Item>
              <Menu.Item key="6" icon={<LogoutOutlined />} onClick={ handleLogout }>Log Out</Menu.Item>
           
            </Menu>
        </Sider>
        <Layout className="site-layout">
          <Header className="site-layout-background">
              <UserOptions />
          </Header>
          <Content style={{ margin: '0 75px' }}>
          <Switch>
          
          <Route exact path='/dash' component={ DashboardUi } />
          <Route exact path='/prestadores' component={ GridPrestadores } />
          <Route exact path='/prestadores/consulta' component={ FilePrestador } />
          <Route exact path='/prestadores/agregar' component={ FormPrestador } />

          <Redirect to="/dash" />
          </Switch>  
          </Content>
          <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer>
        </Layout>
      </Layout>
        </>

    )}